import React from "react";
import Card from "../Card/Card";
import "./CardList.scss";

const cardlist = ({ robots }) => {
  return (
    <div className="CardList">
      {robots.map((user, i) => {
        return (
          <Card
            key={i}
            id={robots[i].id}
            name={robots[i].name}
            email={robots[i].email}
          />
        );
      })}
    </div>
  );
};
export default cardlist;
